import styled from 'styled-components';
// importamos el componente FontAwesomeIcon para poder darle estilos al icono
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';


const colores = {
	borde: "#C2DAFF",
	error: "#bb2929",
	exito: "#1ed12d"
}

const GrupoInput = styled.div`
	position: relative;
	z-index: 90;
`;

const Label = styled.label`
	display: block;
	font-weight: 700;
	min-height: 20px;
	cursor: pointer;
`;

const Input = styled.input`
	width: 100%;
	background: #fff;
	border-radius: 3px;
	height: 40px;
	line-height: 40px;
	padding: 0 40px 0 10px;
	transition: .3s ease all;
	border: 1px solid #CED4DA;
  outline: 4px solid transparent;

  &:focus {
    outline: 4px solid ${colores.borde};
  }

`;

// Agregando estilos al icono
const IconoValidacion = styled(FontAwesomeIcon)`
	position: absolute;
	right: 10px;
	bottom: 12px;
	z-index: 100;
	font-size: 16px;
	opacity: 0;
`;

const IconoError = styled(FontAwesomeIcon)`
  color: red;
`;


const LeyendaError = styled.p`
	font-size: 12px;
	margin-bottom: 0;
	color: ${colores.error};
	display: none;
`;

const ContenedorTerminos = styled.div`
	grid-column: span 2;
  margin: 0 15px;
	input {
		margin-right: 10px;
	}
	@media (max-width: 800px){
		grid-column: span 1;
	}
`;

const MensajeError = styled.div`
	height: 45px;
	line-height: 45px;
	padding: 0px 15px;
	border-radius: 3px;
	grid-column: span 2;
  color: red;
	p {
		margin: 0;
	} 
	b {
		margin-left: 8px;
	}
  display: none;
`;

export { GrupoInput, Input, IconoValidacion, Label, LeyendaError, ContenedorTerminos, MensajeError, IconoError } 