import React from 'react';
import Swal from 'sweetalert2';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrashCan, faPenToSquare } from '@fortawesome/free-solid-svg-icons';
import { MDBDataTable } from 'mdbreact'
import { deleteMascotaById, putMascota } from '../services/mascotas.service';
import Toggle from 'react-toggle';
import "react-toggle/style.css";

const MascotasTabla = ({mascotas, getDataMascotas, setMascotaEditar, setMascotas, modo, setModo }) => {

  const deleteDataMascotaById = async (id) => {
    Swal.fire({
      title: 'Eliminar Mascota?',
      icon: 'error',
      text: 'Se eliminara definitivamente de la base de datos',
      showCancelButton: true
    }).then(({isConfirmed}) => {
      if(isConfirmed){
        deleteMascotaById(id).then(data => {
          if(data.mascota_id){
            setMascotas([]);
            getDataMascotas();
            setModo('crear')
            Swal.fire({
              title: 'Hecho ',
              icon: 'success',
              text: 'Registro eliminado con xito',
              timer: 1500
            })
          }
        });
      }
    })
  }

  const handleChangeToggle = (estado, objMascota) => {
    putMascota({
      ...objMascota,
      mascota_activo: estado,
    }).then(rpta => {
      if(rpta.status === 200){
        
        let nuevasMascotas = mascotas.map((m) => {
          if (m.mascota_id === objMascota.mascota_id) {
            m.mascota_activo = estado;
            if(modo === "editar")
            setMascotaEditar(m);
          }
          return m;
        })

        setMascotas(nuevasMascotas);

        Swal.fire({
          position: 'top-end',
          title: 'Hecho',
          icon: 'success',
          text: 'Estado actualizado con xito',
          showConfirmButton: false,
          timer: 1500
        })
      }
    })
  }

  let data = {
    columns:[
      {
        // Texto del th
        label: 'Id',
        // Nombre del campo que debe coincidir en las rows
        field: 'mascota_id'
      },
      { label: 'Nombre', field: 'mascota_nombre' },
      { label: 'Edad', field: 'mascota_edad'},
      { label: 'Colores', field: 'mascota_colores'},
      { label: 'Activo', field: 'mascota_activo'},
      { label: 'Tipo', field: 'mascota_tipo'},
      { label: 'Raza', field: 'mascota_raza'}, 
      { label: 'Acciones', field: 'acciones'}
    ],
    rows: mascotas.map(m => {
      return {
        ...m,
        mascota_activo: <Toggle defaultChecked={m.mascota_activo} onChange={ (e) => { handleChangeToggle(e.target.checked,{...m}) }}/>,
        acciones: (<>
          <button className='btn btn-secondary mx-2' type='button'><FontAwesomeIcon icon={ faPenToSquare } onClick={ () => { setMascotaEditar({...m}) }}/></button>
          <button className='btn btn-danger' type='button'><FontAwesomeIcon icon={faTrashCan} onClick={() => { deleteDataMascotaById(m.mascota_id) }}/></button>
        </>)
      }
    })
      
  }

  return (
    <div className='container mt-4'>
      <div className="row">
        <div className="col-12">
          <div className="card shadow">
            <div className="card-body">
              <div className="table-responsive">
                {/* Utilizando MDBDataTable */}
                <MDBDataTable className='text-center table align-middle' striped bordered hover data={data}/>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default MascotasTabla