import React, { useEffect, useState } from 'react'
import { getMascotas } from '../services/mascotas.service';
import MascotasForm from './MascotasForm'
import MascotasTabla from './MascotasTabla'

const Mascotas = () => {

const [mascotas, setMascotas] = useState([]);
const [mascotaEditar, setMascotaEditar] = useState(null)
const [modo, setModo] = useState("crear");

  const getDataMascotas = async () => {
    const data = await getMascotas()
    setMascotas(data);
    }

  useEffect(() => {
    getDataMascotas();
  }, [])

  return (
    <>
      <MascotasForm getDataMascotas={getDataMascotas} mascotaEditar={mascotaEditar} modo={modo} setModo={setModo} setMascotas={setMascotas} />
      <MascotasTabla mascotas={mascotas} getDataMascotas={getDataMascotas} setMascotaEditar={setMascotaEditar} setMascotas={setMascotas} mascotaEditar={mascotaEditar} modo={modo} setModo={setModo}/>
    </>
  )
}

export default Mascotas