import React, { useEffect, useState } from 'react'
// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Swal from 'sweetalert2';
import { faCheckCircle, faExclamationTriangle } from '@fortawesome/free-solid-svg-icons'
import { GrupoInput, Input, IconoValidacion, IconoError, Label, LeyendaError, MensajeError } from '../elements/Formularios';
import { postMascota, putMascota } from '../services/mascotas.service';


const  formularioVacio = {
  mascota_nombre: "",
  mascota_colores: "",
  mascota_activo: false,
  mascota_raza: 0,
  mascota_tipo: 0,
  mascota_edad: 0,
}

const MascotasForm = ({getDataMascotas, mascotaEditar, modo, setModo, setMascotas}) => {

  const [formulario, setFormulario] = useState(formularioVacio);

  const handleChange = (e) => {
      let valor = e.target.type === "checkbox" ? e.target.checked : e.target.value;
      setFormulario({
        ...formulario,
        [e.target.name] : valor
      })
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    if(modo === "crear"){
      Swal.fire({
        title: "Registrar mascota?",
        icon: "question",
        text: "Se guardara en la base de datos.",
        showCancelButton: true
      }).then(({ isConfirmed }) => {
        if(isConfirmed){
          postMascota(formulario).then(data => {
            if(data.mascota_id){
              getDataMascotas();
              Swal.fire({
                title: 'Hecho',
                icon: 'success',
                text: 'Mascota registrada con xito',
                timer: 1500
              })
            }
            setFormulario(formularioVacio);
          })
        }
      })
    }else{
      Swal.fire({
        title: 'Actualizar?',
        icon: 'question',
        text: 'Se actualizara el registro en la base de datos',
        showCancelButton: true,
      }).then(({isConfirmed}) => {
        if(isConfirmed){
          console.log(formulario);
          putMascota({...formulario}).then(rpta => {
            if(rpta.status === 200){
              handleCancel();
              setMascotas([]);
              getDataMascotas();
              setModo('crear');
              Swal.fire({
                title: 'Hecho',
                icon: 'success',
                text: 'Registro actualizado con xito',
                timer: 1500
              })
            }
          })
        }
      })
    }
  }

  const handleCancel = () => {
    setFormulario(formularioVacio);
    setModo('crear');
  }

  useEffect(() => {
    if(mascotaEditar){
      setFormulario(mascotaEditar);
      setModo('editar');
    }
  }, [mascotaEditar, setModo])
  

  return (
    <div className='container'>
    <h2 className='display-3 text-primary text-center mt-3'>MascotasApp - Mascotas</h2>
    <hr />
    <div className="row">
      <div className="col-md-12">
        <div className="card shadow">
          <div className="card-body">
            <form className='row' onSubmit={handleSubmit}>
              <div className="form-group col-md-4 mb-2">
                <Label htmlFor="inputNombre">Nombre</Label>
                <GrupoInput>
                  <Input type="text" 
                        id='inputNombre'
                        className="form-control"
                        placeholder='Ej: Toby'
                        name='mascota_nombre'
                        value={formulario.mascota_nombre}
                        onChange={handleChange} />
                  <IconoValidacion icon={faCheckCircle} />
                </GrupoInput>
                <LeyendaError>Lorem ipsum dolor sit amet consectetur adipisicing elit.</LeyendaError>
              </div>
              <div className="form-group col-md-4 mb-2">
                <Label htmlFor="inputEdad">Edad</Label>
                <GrupoInput>
                  <Input type="number"
                        id='inputEdad'
                        min={0}
                        className="form-control"
                        placeholder='Ej: 3'
                        name='mascota_edad'
                        value={formulario.mascota_edad}
                        onChange={handleChange} />
                  <IconoValidacion icon={faCheckCircle} />
                </GrupoInput>
                <LeyendaError>Lorem ipsum dolor sit amet consectetur adipisicing elit.</LeyendaError>
              </div>
              <div className="form-group col-md-4 mb-2">
                <Label htmlFor="inputColores">Colores</Label>
                <GrupoInput>
                  <Input type="text"
                        id='inputColores'
                        className="form-control"
                        placeholder='Ej: marron, caramelo, gris'
                        name='mascota_colores'
                        value={formulario.mascota_colores}
                        onChange={handleChange} />
                  <IconoValidacion icon={faCheckCircle}/>
                </GrupoInput>
                <LeyendaError>Lorem ipsum dolor sit amet consectetur adipisicing elit.</LeyendaError>
              </div>
              <div className="form-group col-md-4 mb-2">
                <Label htmlFor="selectRaza">Raza</Label>
                <GrupoInput>
                  <select id="selectRaza" 
                          className='form-control' 
                          name='mascota_raza'
                          value={formulario.mascota_raza}
                          onChange={handleChange} >  
                    <option value="0"> --- Seleccione --- </option>
                    <option value="1">Cachorrito</option>
                    <option value="2">Viejito</option>
                  </select>
                  <IconoValidacion icon={faCheckCircle}/>
                </GrupoInput>
                <LeyendaError>Lorem ipsum dolor sit amet consectetur adipisicing elit.</LeyendaError>
              </div>
              <div className="form-group col-md-4 mb-2">
                <Label htmlFor="selectTipo">Tipo</Label>
                <GrupoInput>
                  <select id="selectTipo"
                          className='form-control'
                          name='mascota_tipo'
                          value={formulario.mascota_tipo}
                          onChange={handleChange} >
                    <option value="0"> --- Seleccione --- </option>
                    <option value="1">Perro</option>
                    <option value="2">Gato</option>
                  </select>
                  <IconoValidacion icon={faCheckCircle}/>
                </GrupoInput>
                <LeyendaError>Lorem ipsum dolor sit amet consectetur adipisicing elit.</LeyendaError>
              </div>
              <div className="form-group col-md-4 mb-2">
                <Label htmlFor="inputActivo">Registro activo?</Label>
                <GrupoInput>
                  <div className="input-group">
                    <div className="input-group-text">
                      <input className="form-check-input mt-0" 
                            type="checkbox"
                            aria-label="Checkbox for following text input"
                            id='inputActivo'
                            name='mascota_activo'
                            // Para checkbox utilizamos checked que seria analogo a value
                            checked={formulario.mascota_activo}
                            onChange={handleChange} />
                    </div>
                    <label htmlFor="" className='form-control'>Activo</label>
                  </div>
                  <IconoValidacion icon={faCheckCircle}/>
                </GrupoInput>
              </div>              
              {/* <ContenedorTerminos>
                <label htmlFor="terminos">
                  <input type="checkbox" name="terminos" id="terminos" />
                  Acepto los terminos y ondiciones
                </label>
              </ContenedorTerminos> */}
              <MensajeError>
                <p><IconoError icon={faExclamationTriangle}/><b> Error:</b> Por favor rellena el formulario correctamente</p>
              </MensajeError>
              <div className="form-group col-md mt-2">
                <button className='btn btn-success form-control' type='submit'>
                  {
                    modo === 'crear' ? "Registrar" : "Guardar Cambios"
                  }
                </button>
              </div>
              <div className="form-group col-md mt-2">
                <button className='btn btn-danger form-control' type='button' onClick={handleCancel}>Cancelar</button>
              </div>              
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
  )
}

export default MascotasForm