import React from 'react'

const Header = () => {
  return (
    <header>
      <nav className="navbar navbar-expand-sm navbar-dark bg-dark">
        <div className="container-fluid">
          <a className="navbar-brand" href="."><img src="https://patitasbarcelona.com/wp-content/uploads/2020/07/Patitas-Barcelona-Icono.png" style={{width: "35px"}} alt="" /></a>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#collapsibleNavId" aria-controls="collapsibleNavId"
              aria-expanded="false" aria-label="Toggle navigation"><span className='navbar-toggler-icon'></span></button>
          <div className="collapse navbar-collapse" id="collapsibleNavId">
              <ul className="navbar-nav me-auto mt-2 mt-lg-0">
                  <li className="nav-item active">
                      <a className="nav-link" href=".">Home <span className="visually-hidden">(current)</span></a>
                  </li>
                  <li className="nav-item">
                      <a className="nav-link" href=".">Link</a>
                  </li>
                  <li className="nav-item dropdown">
                      <a className="nav-link dropdown-toggle" href="." id="dropdownId" data-bs-toggle="dropdown" aria-expanded="false">Dropdown</a>
                      <div className="dropdown-menu mb-2" aria-labelledby="dropdownId">
                          <a className="dropdown-item" href=".">Action 1</a>
                          <a className="dropdown-item" href=".">Action 2</a>
                      </div>
                  </li>
              </ul>
              <form className="d-flex">
                  <input className="form-control me-2" type="search" placeholder="Search" />
                  <button className="btn btn-outline-success" type="submit">Search</button>
              </form>
          </div>
        </div>
      </nav>
    </header>
  )
}

export default Header